.wrapper{
    background-color: rgb(243, 240, 240);
    width: 100%;
    height: 100vh;
}
.header{
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
}
@media screen and (max-width: 750px) {
    .header{
        justify-content: space-between;
        align-items: flex-end;
        display: flex;
    }
    
}
.button:hover{
    background-color: rgb(209, 185, 47);
}
.button{
    font-size: 20px;
    cursor: pointer;
     background-color: gold;
     height: 30px;
}

@media screen and (max-width: 750px) {
    .navbar{
        display: none;
    }
}

.welcome_image{
    width: 100%;
    height: 300px;
    
}
.img{
    width: 100%;
    height: 100%;
}
.name_of_company{
 justify-content: center;
 align-items: center;
 display: flex;
 height: 50px;
margin-top: 5px;
}
b{
   border-bottom: 2px dashed #fff;
   background-color: #000;
   font-style: italic;
   font-size: 50px;
   color: #fff;
   height: 50px;
   justify-content: center;
   align-items: center;
   display: flex;
}

.details_container{
    font-size: 30px;
    font-weight: bold;
}
.rapheal_world_container{
    justify-content: space-between;
    align-items: space-between;
    display: flex;
    width: 200px;
    height: 30px;
    border-bottom: 2px dashed #fff;
   background-color: #000;
   font-style: italic;
   font-size: 30px;
   color: #fff;
   
}


@media screen AND (max-width: 750px) {
